import * as actions from "../actions/actionTypes";

const initialState = {
  user: null,
  currentUser: null,
};

const userReducer = (state = initialState, action) => {
  switch (action.type) {
    case actions.USER_UPDATE:
      return { ...state, user: action.payload };
    default:
      return state;
  }
};

export default userReducer;
